import React, {Component} from 'react';
import LoginForm from "./LoginForm";
import RegisterForm from "./RegisterForm";
import './css/cssAuth.css'

class MainAuth extends Component {
    addClass = () => {
        const signUpButton = document.getElementById('signUp');
        const signInButton = document.getElementById('signIn');
        const container = document.getElementById('container');

        signUpButton.addEventListener('click', () => {
            container.classList.add('right-panel-active');
        });
        signInButton.addEventListener('click', () => {
            container.classList.remove('right-panel-active');
        });
    };
    render() {
        return (
            <div className="MainAuth">
                <div className="container" id="container">
                    <RegisterForm/>
                    <LoginForm/>
                    <div className="overlay-container">
                        <div className="overlay">
                            <div className="overlay-panel overlay-left">
                                <h1>Welcome Back!</h1>
                                <p> lin kt vi chng ti, vui lng ng nhp bng thng tin c nhn ca bn.</p>
                                <button className="ghost" id="signIn" onClick={this.addClass}>
                                    ng Nhp
                                </button>
                            </div>
                            <div className="overlay-panel overlay-right">
                                <h1>Hello, Friend!</h1>
                                <p>Nhp thng tin c nhn ca bn v bt u hnh trnh vi chng ti.</p>
                                <button className="ghost" id="signUp" onClick={this.addClass}>
                                    ng K
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default MainAuth;
